@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

main {
  width: 77.625rem;
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: 13.438rem;
  font-family: 'Lato', sans-serif;
  font-style: normal;
  margin-top: 5rem;
}
.texto {
  font-weight: 400;
  font-size: 0.875rem;
  color: #171717;
  margin-bottom: 1rem;
}
.texto-destaque {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.logo {
  grid-area: 1 / 1 / 2 / 2;
}

.logo img {
  margin-bottom: 1.75rem;
}
.descricao {
  margin-left: 1.8rem;
}

.caronae {
  margin-left: 2rem;
  grid-area: 1 / 2 / 2 / 3;
}
.apoiadores {
  margin-right: 1rem;
  margin-top: 0.5rem;
  grid-area: 1 / 3 / 2 / 4;
}
.baixar-app {
  translate: 2.5rem -3.5rem;
  grid-area: 1 / 4 / 2 / 5;
}
.img-download {
  height: 3rem;
  margin-right: 1.6rem;
}
.caronae-direitos {
  margin-top: 5rem;
  text-align: center;
}
.caronae-direitos p {
  margin-top: 2.5rem;
}
