@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap%27');

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 200px;
  padding: 28px 50px;
  border: 0;
  cursor: pointer;
}

.buttonDiv {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 50px;
}

.buttonText {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  color: #171717;
}
