@import url('https://fonts.googleapis.com/css2?family=Karla&family=Lato&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

.apoiadoresTitulo {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 54px;
  margin-top: 200px;
  font-weight: 600;
  font-style: normal;
  font-size: 36px;
}
.apoiadoresParagrafo {
  font-family: 'Lato', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  margin-top: 20px;
}
.logosLinha1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logosLinha1 > img {
  padding: 2vw 4vw 5px 4vw;
  margin-top: 25px;
}

.logosLinha2 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logosLinha2 > img {
  padding: 0vw 4vw 0vw 4vw;
}
