.form-text-input,
.form-text-area {
  position: relative;
  display: flex;
  flex-direction: row;
  font-family: Roboto, sans-serif;
  width: 100%;
}

.form-text-input {
  height: 60px;
}

.form-text-area {
  flex: 1;
}

.form-text-input label,
.form-text-area label {
  position: absolute;
  font-size: 12px;
  background-color: #fff;
  padding: 4px;
  color: #1c1b1f;
  top: -10px;
  left: 10px;
}

.form-text-input input,
.form-text-area textarea {
  position: absolute;
  font-size: 16px;
  padding: 8px 0 8px 16px;
  width: 100%;
  height: 100%;
  border-style: solid;
  border-width: 1px;
  border-color: #79747e;
  border-radius: 4px;
}

.form-text-area textarea {
  resize: none;
}

.form-text-input input:focus,
.form-text-area textarea:focus {
  outline: 1px solid #eb72ac;
}

.form-submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 40px;
  border-radius: 40px;
  color: #fff;
  background-color: #2b388a;
  margin-left: auto;

  font-family: Roboto, sans-serif;
  font-weight: 800;
}
